// =========================== >>
// Buttons
// =========================== >>

.btn {
  white-space: normal;
  display: inline-block;
  text-decoration: none;
  text-align: center;

  cursor: pointer;
  border-radius: 5px;
  padding: 12px 18px;
  background-clip: padding-box;
  border: 0px;
  text-transform: uppercase;
  font-size: 13px;
  font-weight: 600;

  &:focus,
  &:hover {
    box-shadow: none;
  }

  &:active {
    box-shadow: none;
  }

  &:disabled {
    cursor: not-allowed;
    filter: brightness(75%);
  }

  &.btn-block {
    display: block;
    width: 100%;
  }

  &.btn-primary {
    background: $ui_theme_bg;
    color: $ui_theme_color;
  }

  &.btn-secondary {
    background: $ui_theme_bg;
    color: $ui_theme_color;
  }

  &.btn-pill {
    flex: 0 0 30px;
    background-color: $ui_button_bg;
    border-radius: 100%;
    height: 30px;
    width: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
  }
}
